export default ".sapMPopupFrame{width:0;height:0;display:none;visibility:visible}.sapMPopupFrameOpen{display:inline}.sapMPopup{min-width:6.25rem;box-sizing:border-box;outline:0;max-width:100%;max-height:100%;background:#fff;border:none;box-shadow:0 .625rem 1.875rem 0 rgba(0,0,0,.15),0 0 0 1px rgba(0,0,0,.15);border-radius:.25rem;min-height:2rem}.sapMPopup .sapMPopupHeader{margin:0;color:#32363a;font-size:1rem;font-weight:400;font-family:\"72\",\"72full\",Arial,Helvetica,sans-serif;border-bottom:1px solid #d9d9d9}.sapMPopup .sapMPopupHeaderText{padding:0 .25rem;text-align:center;height:3rem;line-height:3rem}.sapMPopup .sapMPopupFooter{font-size:1rem;font-weight:400;font-family:\"72\",\"72full\",Arial,Helvetica,sans-serif;background:#fff;border-top:1px solid #d9d9d9;color:#32363a}.sapMPopup .sapMPopupContent{overflow:auto;position:relative;box-sizing:border-box;background-color:#fff;border-radius:.25rem}.sapMPopup .sapMPopupContent,.sapMPopup .sapMPopupFooter{border-bottom-left-radius:.25rem;border-bottom-right-radius:.25rem}.sapMPopup .sapMPopupScroll{vertical-align:middle;box-sizing:border-box;padding:.4375em}.sapUiBLy{background-color:#000;opacity:.6;top:0;left:0;right:0;bottom:0;position:fixed;outline:0 none}.sapMPopupBlockLayer{visibility:visible}.sapMPopupBlockLayerHidden{display:none}";