export default ":host(ui5-yearpicker){display:inline-block;width:100%;height:100%}ui5-yearpicker{display:inline-block;width:100%;height:100%}.sapWCYearPicker{padding:2rem 0 1rem 0;display:flex;flex-direction:column;font-family:\"72\",\"72full\",Arial,Helvetica,sans-serif;font-size:.875rem;justify-content:center;align-items:center}.sapWCYearPickerIntervalContainer{display:flex;justify-content:center;align-items:center;width:100%}.sapWCYearPickerItem{display:flex;margin:0;width:calc(25% - .125rem);height:3rem;color:#fff;background-color:#121212;align-items:center;justify-content:center;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default;outline:0;position:relative;border:1px solid #999;border-radius:0}.sapWCYearPickerItem:hover{background-color:#7a5100}.sapWCYearPickerItem:focus{background-color:#000}.sapWCYearPickerItem.sapWCYearPickerItemSel{background-color:#0f5d94;color:#fff}.sapWCYearPickerItem.sapWCYearPickerItemSel:focus{background-color:#0a4066}.sapWCYearPickerItem.sapWCYearPickerItemSel:focus::after{border-color:#fff}.sapWCYearPickerItem.sapWCYearPickerItemSel:hover{background-color:#0a4066}.sapWCYearPickerItem:focus::after{content:\"\";position:absolute;width:calc(100% - .25rem);height:calc(100% - .25rem);border:2px dotted #fff;top:0;left:0}.sapUiSizeCompact .sapWCYearPickerItem{height:2rem}";