export default ":host(ui5-yearpicker){display:inline-block;width:100%;height:100%}ui5-yearpicker{display:inline-block;width:100%;height:100%}.sapWCYearPicker{padding:2rem 0 1rem 0;display:flex;flex-direction:column;font-family:\"72\",\"72full\",Arial,Helvetica,sans-serif;font-size:.875rem;justify-content:center;align-items:center}.sapWCYearPickerIntervalContainer{display:flex;justify-content:center;align-items:center;width:100%}.sapWCYearPickerItem{display:flex;margin:1px;width:calc(25% - .125rem);height:3rem;color:#333;background-color:#f7f7f7;align-items:center;justify-content:center;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default;outline:0;position:relative;border:none;border-radius:0}.sapWCYearPickerItem:hover{background-color:#f7f7f7}.sapWCYearPickerItem:focus{background-color:#d9d9d9}.sapWCYearPickerItem.sapWCYearPickerItemSel{background-color:#427cac;color:#fff}.sapWCYearPickerItem.sapWCYearPickerItemSel:focus{background-color:#346187}.sapWCYearPickerItem.sapWCYearPickerItemSel:focus::after{border-color:#fff}.sapWCYearPickerItem.sapWCYearPickerItemSel:hover{background-color:#346187}.sapWCYearPickerItem:focus::after{content:\"\";position:absolute;width:calc(100% - .375rem);height:calc(100% - .375rem);border:1px dotted #000;top:2px;left:2px}.sapUiSizeCompact .sapWCYearPickerItem{height:2rem}";