export default ":host(ui5-calendar-header){display:inline-block;width:100%}ui5-calendar-header{display:inline-block;width:100%}.sapWCCalHead{display:flex;height:3rem;padding:.25rem 0;box-sizing:border-box}.sapWCCalHead ui5-button{height:100%}.sapWCCalHeadArrowButton{display:flex;justify-content:center;align-items:center;width:2.5rem;background-color:transparent;color:#0854a0;cursor:pointer;overflow:hidden;white-space:nowrap;padding:0;font-size:.875rem}.sapWCCalHeadArrowButton:focus{outline:0}.sapWCCalHeadArrowButton:hover{background-color:#ebf5fe;color:#0854a0}.sapWCCalHeadArrowButton:active{background-color:#0854a0;color:#fff}.sapWCCalHeadArrowButton,.sapWCCalHeadMiddleButton{border:none;border-radius:.25rem}.sapWCCalHeadMidButtonContainer{display:flex;justify-content:space-around;flex:1;padding:0 .5rem}.sapWCCalHeadMidButtonContainer .sapWCCalHeadMiddleButton:first-child{margin-right:.5rem}.sapWCCalHeadMiddleButton{font-family:\"72\",\"72full\",Arial,Helvetica,sans-serif;width:2.5rem;flex:1;position:relative;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.sapWCCalHeadMiddleButton:focus{border:none;border-radius:.25rem}.sapWCCalHeadMiddleButton:focus::after{content:\"\";display:block;width:calc(100% - .375rem);height:calc(100% - .375rem);border:1px dotted #000;position:absolute;top:.125rem;left:.125rem}.sapWCCalHeadMiddleButton:focus:active::after{border-color:#fff}.sapUiSizeCompact .sapWCCalHead{height:2rem;padding:0}.sapUiSizeCompact .sapWCCalHeadArrowButton{width:2rem}[dir=rtl] .sapWCCalHeadMidButtonContainer .sapWCCalHeadMiddleButton:first-child{margin-left:.5rem;margin-right:0}";