export default ":host(ui5-month-picker){display:inline-block;width:100%;height:100%}ui5-month-picker{display:inline-block;width:100%;height:100%}.sapWCMonthPicker{padding:2rem 0 1rem 0;display:flex;flex-direction:column;font-family:\"72\",\"72full\",Arial,Helvetica,sans-serif;font-size:.875rem;justify-content:center;align-items:center}.sapWCMonthPickerItem{display:flex;width:calc(33.333% - .125rem);height:3rem;color:#fff;background-color:#121212;align-items:center;justify-content:center;margin:0;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default;outline:0;position:relative;border:1px solid #999;border-radius:0}.sapWCMonthPickerItem:hover{background-color:#7a5100}.sapWCMonthPickerItem.sapWCMonthPickerItemSel{background-color:#0f5d94;color:#fff}.sapWCMonthPickerItem.sapWCMonthPickerItemSel:focus{background-color:#0a4066}.sapWCMonthPickerItem.sapWCMonthPickerItemSel:focus::after{border-color:#fff}.sapWCMonthPickerItem.sapWCMonthPickerItemSel:hover{background-color:#0a4066}.sapWCMonthPickerItem:focus{background-color:#000}.sapWCMonthPickerItem:focus::after{content:\"\";position:absolute;width:calc(100% - .25rem);height:calc(100% - .25rem);border:2px dotted #fff;top:0;left:0}.sapWCMonthPickerQuarter{display:flex;justify-content:center;align-items:center;width:100%}.sapUiSizeCompact .sapWCMonthPickerItem{height:2rem}";