export default ":host(ui5-table-row){display:inline-block;width:100%}ui5-table-row{display:inline-block;width:100%}.sapWCTableRow{display:grid;align-items:center;background-color:#fff;box-sizing:border-box}.sapWCTableRow:focus{outline:1px solid #000;outline-offset:-3px;outline-style:dotted}.sapWCTablePopinRow{padding:.25rem 1rem;grid-column-start:1;display:flex;flex-direction:column;align-items:flex-start}.sapWCTablePopinTitle{color:#333}.sapMWCTableRowCellContainer{height:100%;padding:.25rem;box-sizing:border-box}.sapWCTableRowWithBorder{border-bottom:1px solid #e5e5e5}";