export default ":host(ui5-month-picker){display:inline-block;width:100%;height:100%}ui5-month-picker{display:inline-block;width:100%;height:100%}.sapWCMonthPicker{padding:2rem 0 1rem 0;display:flex;flex-direction:column;font-family:\"72\",\"72full\",Arial,Helvetica,sans-serif;font-size:.875rem;justify-content:center;align-items:center}.sapWCMonthPickerItem{display:flex;width:calc(33.333% - .125rem);height:3rem;color:#333;background-color:#f7f7f7;align-items:center;justify-content:center;margin:1px;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default;outline:0;position:relative;border:none;border-radius:0}.sapWCMonthPickerItem:hover{background-color:#f7f7f7}.sapWCMonthPickerItem.sapWCMonthPickerItemSel{background-color:#427cac;color:#fff}.sapWCMonthPickerItem.sapWCMonthPickerItemSel:focus{background-color:#346187}.sapWCMonthPickerItem.sapWCMonthPickerItemSel:focus::after{border-color:#fff}.sapWCMonthPickerItem.sapWCMonthPickerItemSel:hover{background-color:#346187}.sapWCMonthPickerItem:focus{background-color:#d9d9d9}.sapWCMonthPickerItem:focus::after{content:\"\";position:absolute;width:calc(100% - .375rem);height:calc(100% - .375rem);border:1px dotted #000;top:2px;left:2px}.sapWCMonthPickerQuarter{display:flex;justify-content:center;align-items:center;width:100%}.sapUiSizeCompact .sapWCMonthPickerItem{height:2rem}";